// import pagitionMixin from '@/utils/pagitionMixin.js';
import TableColumnSieving from '@/components/tableColumnSieving/TableColumnSieving.vue';

export default {
    inheritAttrs: false,
    props: {
        // 
        selection: {
            type: Boolean,
            default: false
        },
        // 
        showIndex: {
            type: Boolean,
            default: false
        },
        showIndexLabel: {
            type: String,
            default: ''
        },
        // 
        expand: {
            type: Boolean,
            default: false
        },
        //
        ajaxData: {
            type: Object,
            default () {
                return {};
            }
        },
        // 
        columns: {
            type: Array,
            default () {
                return [];
            }
        },
        // 
        ajaxRequestName: {
            type: String,
            default: 'ajaxTableData'
        },
        showSearchInput: { //
            default () {
                return true;
            }
        },
        showRefresh: { ////
            default () {
                return true;
            }
        },
        showColumnBtn: { //
            default () {
                return true;
            }
        },
        // 
        pagerCountParams: {
            type: Number,
            default: 9
        },
        // 
        operateContentVisible: {
            type: Boolean,
            default: true
        },
        // 
        paginationVisible: {
            type: Boolean,
            default: true
        },
        tableConStyle: {
            default () {
                return {}
            }
        },
        // 
        isNotParent: {
            default: true,
        },
        selectable: {
            type: Boolean,
            default: false
        },
        selectInit: {
            type: Function
        },
        // 
        loadingStyle: {
            type: Object,
            default () {
                return {
                    lock: true,
                    text: '......',
                    // spinner: 'el-loading-spinner',
                    background: 'rgba(255,255,255,.9)'
                }
            }
        }
    },
    components: {
        TableColumnSieving
    },
    data() {
        return {
            tableData: [],
            // tableData: [
            //     { date: '1', name: '2', address: '3' },
            //     { date: '1', name: '2', address: '3' },
            //     { date: '1', name: '2', address: '3' },
            //     { date: '1', name: '2', address: '3' },
            //     { date: '1', name: '2', address: '3' },
            // ],
            // columns: [
            //     { prop: 'date', label: '', minWidth: '120px', sortable: true, },
            //     { prop: 'name', label: '', minWidth: '80px', sortable: true, },
            //     { prop: 'address', label: '', minWidth: '80px', sortable: true, },
            // ]
            pageSizes: [10, 20, 30, 50], //
            pageSize: 10, //
            pagerCount: 7,
            pageLayout: 'total, sizes, prev, pager, next, jumper',
            pageLayoutSimple: 'total, prev, pager, next', //
            totalPage: 0, //
            currentPage: 1, //1
            searchInput: '', //  
            searchInputObj: { //searchInput
                input: ''
            },
            //ajaxloading
            loading: false,
            loadingObj: {
                lock: true,
                text: '',
                spinner: 'el-loading-spinner', //el-loading-spinner   el-icon-loading
                background: 'rgba(255,255,255,.9)'
            },
            loadingInstance: null,
            pageKey: 1,
            operateType: 0, //0->1->2->
        };
    },
    watch: {
        ajaxData: {
            deep: true,
            handler(newVal) {
                // this.loading = false;
                this.totalPage = newVal.totalRecords || 0;
                this.tableData = newVal.data || [];
            }
        },
        currentPage(a, b) {
            // debugger
            console.log(a, b);
        }
    },
    methods: {
        handleSelect(selection, row) {
            this.$emit('emitSelect', selection, row);
        },
        handleSelectAll(selection, row) {
            this.$emit('emitSelectAll', selection);
        },
        // 
        handleSelectionChange(val) {
            this.$emit('emitSelectionChange', val);
        },
        //
        handleRowClick(row, column, event) {
            this.$emit('emitRowClick', row, column, event);
        },
        // 
        handleCellClick(row, column, cell, event) {
            this.$emit('emitCellClick', row, column, cell, event);
        },
        // 
        handleCellDblclick(row, column, cell, event) {
            this.$emit('emitCellDblclick', row, column, cell, event);
        },
        handleTableCurrentChange(val) {
            this.$emit('emitTableCurrentChange', val);
        },
        // 
        handleSizeChange(val) {
            this.pageSize = val;
            this.ajaxTableData();
        },
        // 
        handleCurrentChange(val) {
            this.$emit('emitCurrentChange', val);
            this.currentPage = val;
            this.ajaxTableData();
        },
        // 
        ajaxTableData() {
            let ajaxName = this.ajaxRequestName;
            let pAjaxMethod = this.$parent[ajaxName];
            if (this.isNotParent && pAjaxMethod) {
                pAjaxMethod();
            } else if (this.isNotParent == false || !pAjaxMethod) {
                let pageParams = this.getParams();
                this.$emit('emitAjaxTableData', pageParams);
            }
        },
        // 
        // obj  delNum 1
        dealDelPageNum(obj) {
            let delNum = 1;
            if (obj && obj.delNum) {
                delNum = obj.delNum;
            }
            let pageParams = this.getParams();
            let totalRecords = this.ajaxData.totalRecords
            let maxPageNum = Math.ceil((totalRecords - delNum) / pageParams.limit);
            if (maxPageNum < this.currentPage) {
                this.currentPage = Number(maxPageNum);
            }
        },
        // 
        setCurrentPage(page) {
            if (typeof page == 'string') {
                page = Number(page);
            }
            this.currentPage = page > 0 ? page : 1;
            this.pageKey += 1;
        },
        //  init:true  
        getParams(init = '') {
            let operateType = this.operateType;
            if (init === true) {
                this.searchInputObj.input = '';
                this.currentPage = 1;
                this.pageSize = 10;
            }
            this.operateType = 0;
            return {
                limit: this.pageSize,
                page: this.currentPage,
                condition: this.searchInputObj.input,
                operateType: operateType
            };
        },
        // 
        handleSearch() {
            this.currentPage = 1;
            this.operateType = 1;
            this.ajaxTableData();
        },
        // 
        handleRefresh() {
            this.operateType = 2;
            this.searchInputObj.input = '';
            this.currentPage = 1;
            this.ajaxTableData();
        },
        showLoading(isShow) {
            if (isShow) {
                this.loadingInstance = this.$loading(this.loadingStyle);
            } else {
                this.loadingInstance && this.loadingInstance.close();
            }
        }
    },
    mounted() {
        this.totalPage = this.ajaxData.totalRecords || 0;
        this.tableData = this.ajaxData.data || [];
        // let _this = this;
        // window.onresize = function() {
        //     console.log('------');
        //     _this.$nextTick(() => {
        //         _this.$refs.selfInnerTable.doLayout();
        //     });
        // }
    }
};