import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import layoutRouterConfig from './routerConfig/layout.js';
import functionRouterConfig from './routerConfig/function.js';
import toolRouterConfig from './routerConfig/tool.js';
import mapRouterConfig from './routerConfig/map.js';

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Home",
        component: Home,
        redirect: '/layout',
        meta: {
            name: '',
            noCrumb: true, //
            noCrumbLink: true, //
            // noMenu: true, //
            // hiddenHeader: true, //
            // hiddenLeftMenu: true, //false,
            // hiddenCrumbs: true, //false,,
        },
        children: [{
            path: '/singleImage',
            name: '/singleImage',
            component: () => import('../views/main/singleImage/singleImage.vue'),
            meta: {
                name: '',
                noMenu: true,
            }
        }, {
            path: '/layout',
            name: '/layout',
            redirect: '/layout/style1',
            component: () => import('../views/main/layout/layout.vue'),
            meta: {
                name: ''
            },
            children: layoutRouterConfig
        }, {
            path: '/function',
            name: '/function',
            redirect: '/function/function1',
            component: () => import('../views/main/function/function.vue'),
            meta: {
                name: ''
            },
            children: functionRouterConfig
        }, {
            path: '/tool',
            name: '/tool',
            redirect: '/tool/routers',
            component: () => import('../views/main/tool/tool.vue'),
            meta: {
                name: ''
            },
            children: toolRouterConfig
        }, {
            path: '/map',
            name: '/map',
            redirect: '/map/leaflet',
            component: () => import('../views/main/map/map.vue'),
            meta: {
                name: '',
            },
            children: mapRouterConfig
        }]
    },
    // {
    //     path: "/about",
    //     name: "About",
    //     // route level code-splitting
    //     // this generates a separate chunk (about.[hash].js) for this route
    //     // which is lazy-loaded when the route is visited.
    //     component: () =>
    //         import( /* webpackChunkName: "about" */ "../views/About.vue")
    // }
];

const router = new VueRouter({
    routes
});

export default router;