//http request 
axios.interceptors.request.use(
    config => {
        // debugger
        // baseURL;
        config.baseURL = config.baseURL === undefined ? baseURL : config.baseURL;

        // get
        // headerscontentType,
        if (!config.headers) {
            config.headers = { 'Content-Type': 'application/x-www-form-urlencoded' };
        }
        if (config.method == "get" && config.params) {
            Object.keys(config.params).forEach(item => {
                config.params[item] = config.params[item];
            });
        }

        // postdata
        config.transformRequest[0] = function(data) {
            // multiple/form-data
            if (data && typeof data.get === 'function') {
                return data;
            }
            if (data) {
                // debugger
                // postbody,application/json;
                if (config.headers['Content-Type'] &&
                    config.headers['Content-Type'].includes('application/json')) {
                    return JSON.stringify(data);
                }
                // config.headers.post = { 'Content-Type': 'application/json;', 'Accept': '*/*' };
                // postapplication/x-www-form-urlencoded
                let res = [];
                for (let it in data) {
                    // 
                    // res.push(encodeURIComponent(it) + '=' + encodeURIComponent(data[it]));
                    res.push(it + '=' + data[it]);
                }
                return res.join('&');
            }
            // data = Qs.stringify(data);
            return data;
        };
        return config;
    },
    error => {
        return Promise.reject(err);
    }
);

//http response 
axios.interceptors.response.use(
    response => {
        //         
        return response.data;
    },
    error => {
        let response = error.response;
        let msg = (response.data && response.data.message) || error.response.statusText;
        Message({
            type: 'error',
            showClose: true,
            message: msg
        });
        return Promise.reject(msg);
    }
);

/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */
const get = (url, params = {}, otherParams = {}) => {
    return new Promise((resolve, reject) => {
        axios.get(url, {
                params: params,
                ...otherParams
            })
            .then(response => {
                resolve(response);
            })
            .catch(err => {
                reject(err);
            });
    });
};
/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */
const post = (url, data = {}, otherParams = {}) => {
    return new Promise((resolve, reject) => {
        // axios.post(url, data).then(
        axios({
            method: 'post',
            url,
            data: data,
            ...otherParams
        }).then(
            response => {
                resolve(response);
            },
            err => {
                reject(err);
            }
        );
    });
};

/**
 * delete
 * @param url
 * @param data
 * @returns {Promise}
 */
const deleteRequest = (url, data = {}, otherParams = {}) => {
    return new Promise((resolve, reject) => {
        axios({
            method: 'delete',
            url,
            data: data,
            ...otherParams
        }).then(
            response => {
                resolve(response);
            },
            err => {
                reject(err);
            }
        );
    });
};

// 
Vue.prototype.$baseURL = baseURL;
Vue.prototype.$get = get;
Vue.prototype.$post = post;
Vue.prototype.$delete = deleteRequest;
Vue.prototype.$axios = axios;