import selfSubMenu from './subMenu.vue';

export default {
    components: { selfSubMenu },
    props: {
        menuList: Array,
        mode: {
            type: String,
            default: 'horizontal' //horizontal / vertical
        },
        defaultActiveIndex: {
            default: ''
        },
        keyName: {
            type: String,
            default: 'self-menu-001'
        }
    },
    data() {
        return {
            activeIndex: '',
            keyVal: 1,
            menuData: [],
        }
    },
    watch: {
        menuList: {
            deep: true,
            handler(newVal) {
                this.menuData = [];
                this.menuData = newVal;
                this.keyVal++;
                this.dealActiveIndex();
            }
        },
        $route(to, from) {
            this.dealActiveIndex();
        }
    },
    methods: {
        dealActiveIndex(type) {
            this.$nextTick(() => {
                this.activeIndex = this.$route.path;
            });

            //     debugger
            //     let curPath = this.$route.path;
            //     let allMenuData
            //     let menuData = this.menuData.flat(true);

            //     let filterArr = menuData.filter(item => item.path == );
            //     let activeData = filterArr[0];
            //     if (activeData) {
            //         this.activeIndex = activeData.path;
            //     }
            //     this.forEachMenu(this.menuData, curPath);
        },
        handleClickMenu(menu, e) {
            e.stopPropagation();
            window.open(menu.isLinkUrl, '_blank');
            return false;
        }
    },
    created() {
        this.menuData = this.menuList;
        if (this.defaultActiveIndex) {
            this.activeIndex = this.defaultActiveIndex;
        } else {
            this.dealActiveIndex();
        }
    }
}