<template>
    <el-menu :key="keyName+'-'+keyVal" :default-active="activeIndex" class="self-menu-list-container" :mode="mode"
        background-color="#323330" text-color="#ffffff" active-text-color="#ffd04b" router :collapse-transition="false">
        <template v-for="(menu,index) of menuData">
            <template v-if="menu.children&&menu.children.length>0">
                <self-sub-menu :key="'sub-'+index+menu.path" :data="menu">
                </self-sub-menu>
            </template>
            <template v-else>
                <el-menu-item :key="'self-menu-'+index" :index="menu.path"
                    :class="{'el-submenu-link-url':menu.isLinkUrl}" :disabled="menu.disabled">
                    <template v-if="menu.isLinkUrl">
                        <span class="link-url" @click="handleClickMenu(menu,$event)"> {{menu.name}}</span>
                    </template>
                    <template v-else>
                        {{menu.name}}
                    </template>
                </el-menu-item>
            </template>
        </template>
    </el-menu>
</template>

<script>
    import _script from './index.js';
    export default _script;
</script>

<style lang="less">
    @import url('./style.less');
</style>